table {
    width: 100%;
    border-collapse: collapse;
    white-space: nowrap;
}

thead {
    background-color: #f5f5f5;
}

th, td {
    border: 1px solid #e0e0e0;
    padding: 8px 16px;
    text-align: left;
}

tr:nth-child(even) {
    background-color: #f9f9f9;
}

tr:nth-child(odd) td:nth-child(1), tr:nth-child(odd) td:nth-child(2) {
    background-color: #f5edff;
}

tr:nth-child(even) td:nth-child(1), tr:nth-child(even) td:nth-child(2) {
    background-color: #f9f9f9;
}

.filterInput {
    margin-bottom: 30px;
}

.pagination {
    margin-top: 30px;
}

.tableWrapper {
    overflow-x: auto;
}

th:nth-child(1), th:nth-child(2), 
td:nth-child(1), td:nth-child(2) {
    position: sticky;
    left: 0;
    z-index: 2;  /* Pour qu'il apparaisse au-dessus des autres cellules */
}

th:nth-child(1), th:nth-child(2) {
    background-color: #f5f5f5; 
}

td:nth-child(1)::after, td:nth-child(2)::after, 
th:nth-child(1)::after, th:nth-child(2)::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 1px;
    background-color: #e0e0e0;
}

td:nth-child(1)::before, td:nth-child(2)::before, 
th:nth-child(1)::before, th:nth-child(2)::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    background-color: #e0e0e0;
}
